.app {
  margin: 0 auto;
  height: 100%;
  width: 100%;
  z-index: 0;
}

.app-hdr {
  margin: 0 auto;
  text-align: center;
  /*height: 100px;*/
  /*padding-top: 100px; */
  /*padding-bottom: 50px;*/
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  background:
    -moz-linear-gradient(bottom,  #111 0%, rgba(0,0,0,.5) 99%),
    -moz-linear-gradient(left,  rgba(204,0,0,0) 0%, rgba(204,0,0,0.5) 20%, rgba(204,0,0,0) 100%),
    -moz-linear-gradient(top, rgba(0,0,0,.5) 0%, rgba(0,0,0,.2) 100%),
    rgba(0,230,223,.6);

  background:
    -webkit-linear-gradient(bottom,  #111 0%, rgba(0,0,0,.5) 99%),
    -webkit-linear-gradient(left,  rgba(204,0,0,0) 0%, rgba(204,0,0,0.5) 20%, rgba(204,0,0,0) 100%),
    -webkit-linear-gradient(top, rgba(0,0,0,.5) 0%, rgba(0,0,0,.2) 100%),
    rgba(0,230,223,.6);

  background:
    linear-gradient(to top,  #111 0%, rgba(0,0,0,.5) 99%),
    linear-gradient(to right,  rgba(204,0,0,0) 0%, rgba(204,0,0,0.5) 20%, rgba(204,0,0,0) 100%),
    linear-gradient(to bottom, rgba(0,0,0,.5) 0%, rgba(0,0,0,.2) 100%),
    rgba(0,230,223,.6);

  box-sizing: border-box;
  border-bottom: 3px solid #cc0000;
  z-index: 1000;
  /*border-bottom: 1px solid #ddd;*/

}

.app-hdr__grid {
  width: 90%;
  margin: 35px auto 0;
  display: grid;
  grid-template-columns: 20% 25% 55%;
  grid-template-rows: 100%;
  grid-column-gap: 10px;
  grid-row-gap: 10px;
}

.app-hdr__grid-item {
  margin: 0 auto;
  padding: 0;
  text-align: center;
  box-sizing: border-box;
  width: 100%;
  position: relative;
  /*border: 1px solid orange;*/
}

.app-hdr__logo {
  background: url('me-logo.svg');
  background-size: contain;
  background-position: center top;
  background-repeat: no-repeat;
  width: 100%;
  z-index: 200;
  /*border: 1px solid green;*/
}

.app-hdr__nav {
  /*margin-top: 45px;*/
  margin: 0 auto;
  padding: 0;
  line-height: 1em;
  z-index: 10000;
  /*border: 1px solid rgba(0,0,0,0);*/
  display: block;
}

.app-hdr__name {
  color: #fff;
  font-family: 'Roboto Condensed';
  font-weight: 400;
  font-style: italic;
  text-transform: uppercase;
  font-size: calc(12px + 2vw);
  text-shadow: 0 0 10px rgba(204,0,0,1);
  text-decoration: none;
  box-sizing: border-box;
  position: relative;
  top: -5px;
  left: auto;
  z-index: 1001;
}
.app-hdr__name:hover {
  text-shadow: 
    0 0 20px rgba(204,0,0,1),
    0 0 10px rgba(204,0,0,1),
    0 0 5px rgba(204,0,0,1);
}

.app-hdr__links {
  margin: 0 10px;
  padding: 7px 10px;
  text-decoration: none;
  color:#fff;
  font-size: 2vmin;
  font-family: 'Roboto Condensed';
  font-weight: 700;
  line-height: 1em;
  z-index: 1300;
  /*create a gradient bg that is 2X tall the link
    then, position move upward on hover to create effect*/
  background: linear-gradient(
    to top, 
    rgba(204,0,0,1),
    rgba(204,0,0,1) 50%,
    rgba(204,0,0,0) 50%, 
    rgba(204,0,0,0)
    );
  transition: 0.2s;
  background-size: 100% 200%;
  border-bottom: 2px solid #cc0000;
}

.app-hdr__links:hover {
  background-position: 100% 100%;
}

.app-body {
  /* margin-top = app-hdr height + app-body__title height */
  /* change the media query val too */
  padding-top: calc(14vmin + 14vmax);
  box-sizing: border-box;
  z-index: 0;
}




.app-hdr--move-up {
  -webkit-animation: anim_app-hdr-move-up;
          animation: anim_app-hdr-move-up;
  -webkit-animation-duration: .3s;
          animation-duration: .3s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
  -webkit-animation-timing-function: ease-in;
          animation-timing-function: ease-in;
}

.app-hdr--move-down {
  -webkit-animation: anim_app-hdr-move-down;
          animation: anim_app-hdr-move-down;
  -webkit-animation-duration: .3s;
          animation-duration: .3s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
  -webkit-animation-timing-function: ease-out;
          animation-timing-function: ease-out;
}

.app-hdr__logo--init {
/*  margin-top: -50%;
  margin-right: -50px;*/
  -webkit-animation: anim_logo-init;
          animation: anim_logo-init;
  -webkit-animation-duration: .3s;
          animation-duration: .3s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
  -webkit-animation-timing-function: ease-in;
          animation-timing-function: ease-in;
}

.app-hdr__logo--small {
  -webkit-animation: anim_logo-small;
          animation: anim_logo-small;
  -webkit-animation-duration: .3s;
          animation-duration: .3s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
  -webkit-animation-timing-function: ease-in;
          animation-timing-function: ease-in;
/*  -webkit-animation-delay: .2s;
          animation-delay: .2s;*/
}

.app-hdr__logo--big {
  -webkit-animation: anim_logo-big;
          animation: anim_logo-big;
  -webkit-animation-duration: .3s;
          animation-duration: .3s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
  -webkit-animation-timing-function: ease-in;
          animation-timing-function: ease-in;
/*  -webkit-animation-delay: .2s;
          animation-delay: .2s;*/
}









@media only screen and (min-width: 1200px) {
  .app-hdr {
    padding-top: 100px; 
    padding-bottom: 30px;
    /*border-bottom: 3px solid #cc0000;*/
  }

  .app-hdr__logo--init {
    margin-top: -45%;
    margin-right: -50px;
  }

  @keyframes anim_app-hdr-move-up {
    0% { padding-top: 100px; padding-bottom: 50px; }
    100% { padding-top: 12px; padding-bottom: 8px; }
  }

  @keyframes anim_app-hdr-move-down {
    0% { padding-top: 12px; padding-bottom: 8px; }
    100% { padding-top: 100px; padding-bottom: 50px; }
  }

  @keyframes anim_logo-init {
    0% { width: 100%; height: 8vw; opacity: 0; }
    100% { width: 20vw; height: 18vw; opacity: 1; }
  }

  @keyframes anim_logo-small {
    0% { width: 100%; height: 18vw; margin-top: -45%; margin-right: -80px; }
    100% { width: 100%; height: 8vw; margin-top: -20%; margin-right: -100px; }
  }

  @keyframes anim_logo-big {
    0% { width: 100%; height: 8vw; margin-top: -20%; margin-right: -100px; }
    100% { width: 100%; height: 18vw; margin-top: -45%; margin-right: -80px; }
  }
}




@media only screen and (min-width: 768px) and (max-width: 1199px) {
  .app-hdr {
    padding-top: 80px; 
    padding-bottom: 30px;
    /*border-bottom: 3px solid green;*/
  }

  .app-hdr__logo--init {
    margin-top: -50%;
    margin-right: -50px;
  }

  .app-body {
    padding-top: 220px;
  }

  @keyframes anim_app-hdr-move-up {
    0% { padding-top: 80px; padding-bottom: 30px; }
    100% { padding-top: 0px; padding-bottom: 0px; }
  }

  @keyframes anim_app-hdr-move-down {
    0% { padding-top: 0px; padding-bottom: 0px; }
    100% { padding-top: 80px; padding-bottom: 30px; }
  }

  @keyframes anim_logo-init {
    0% { width: 100%; height: 8vw; opacity: 0; }
    100% { width: 20vw; height: 18vw; opacity: 1; }
  }

  @keyframes anim_logo-small {
    0% { width: 100%; height: 18vw; margin-top: -50%; margin-right: -80px; }
    100% { width: 100%; height: 8vw; margin-top: -20%; margin-right: -100px; }
  }

  @keyframes anim_logo-big {
    0% { width: 100%; height: 8vw; margin-top: -20%; margin-right: -100px; }
    100% { width: 100%; height: 18vw; margin-top: -50%; margin-right: -80px; }
  }

}



@media only screen and (min-width: 0px) and (max-width: 767px) {

  .app-hdr {
    padding-top: 80px; 
    padding-bottom: 30px;
    /*border-bottom: 3px solid purple;*/
    background:
      -moz-linear-gradient(bottom, #111 0%, rgba(0,0,0,.4) 100%),
      -moz-linear-gradient(left, rgba(204,0,0,0) 0%, rgba(204,0,0,0.5) 50%, rgba(204,0,0,0) 100%),
      -moz-linear-gradient(top, rgba(0,0,0,.4) 0%, rgba(0,0,0,.2) 100%),
      rgba(0,230,223,.4);

    background:
      -webkit-linear-gradient(bottom, #111 0%, rgba(0,0,0,.4) 100%),
      -webkit-linear-gradient(left, rgba(204,0,0,0) 0%, rgba(204,0,0,0.5) 50%, rgba(204,0,0,0) 100%),
      -webkit-linear-gradient(top,rgba(0,0,0,.4) 0%, rgba(0,0,0,.2) 100%),
      rgba(0,230,223,.4);

    background:
      linear-gradient(to top, #111 0%, rgba(0,0,0,.4) 100%),
      linear-gradient(to right, rgba(204,0,0,0) 0%, rgba(204,0,0,0.5) 50%, rgba(204,0,0,0) 100%),
      linear-gradient(to bottom, rgba(0,0,0,.4) 0%, rgba(0,0,0,.2) 100%),
      rgba(0,230,223,.4);
  }

  .app-hdr__grid.mobile {
    display: block;
    margin-top: calc(5px + 1vmin);
    width: 100%;
  }

  .app-hdr__grid-item {
    display: block;
  }

  .app-hdr__name {
    font-size: calc(13px + 2vw);
    display:block;
    margin-top: -10px;
  }
  .app-hdr--move-down .app-hdr__name {
    margin-top: -10px;
  }
  .app-hdr--move-up .app-hdr__name {
    margin-top: 0px;
  }

  .app-hdr__nav {
    margin-top: 10px;
  }
  .app-hdr--move-down .app-hdr__nav {
    margin-top: 10px;
    margin-bottom: 0;
  }
  .app-hdr--move-up .app-hdr__nav {
    margin-top: 0px;
    margin-bottom: 5px;
  }

  .app-hdr__links {
    font-size: calc(7px + 2vmin);
    line-height: 2em;
  }

  .app-hdr__logo--init {
    margin-right: auto;
    margin-left: auto;
  }

  .app-hdr__logo.mobile {
    margin: 0 auto;
    display: block;
  }

  .app-body {
    padding-top: 240px;
  }

  @keyframes anim_app-hdr-move-up {
    0% { padding-top: 80px; padding-bottom: 30px; }
    100% { padding-top: 5px; padding-bottom: 5px; }
  }

  @keyframes anim_app-hdr-move-down {
    0% { padding-top: 5px; padding-bottom: 5px; }
    100% { padding-top: 80px; padding-bottom: 30px; }
  }

  @keyframes anim_logo-init {
    0% { width: 100%; height: 10vw; opacity: 0; }
    100% { width: 20vw; height: 20vw; opacity: 1; }
  }

  @keyframes anim_logo-small {
    0% { width: 100%; height: 20vw; margin-top: -10%; opacity: 1; }
    100% { width: 100%; height: 0; margin-top: 0%; opacity: 0; }
  }

  @keyframes anim_logo-big {
    0% { width: 100%; height: 0; margin-top: 0%; opacity: 0; }
    100% { width: 100%; height: 20vw; margin-top: -10%; opacity: 1; }
  }

}

