/***** EXAMPLES ******/

.examples {
  /*position: relative;*/
  /*top: 300px;*/
}

.ex__wrap {
  margin: 0 auto 30px;
  /*background: #b8c4ae;*/
  font-size: calc(1em + 1vmin);
  color: #677064;
}

.ex__row1 {
  grid-row-start: 1;
  grid-row-end: 2;
  grid-column-start: 1;
  grid-column-end: 2;
  display: grid;
  grid-template-rows: 1fr;
  grid-template-columns: 75% 25%;
  padding: 70px 0;
  text-align: center;
}

.ex__row1-left {
  grid-area: 1 / 1 / 2 / 2;
  padding: 2%;
  max-width: 600px;
  margin: auto;
}

.ex__row1-right {
  grid-area: 1 / 2 / 2 / 3;
  box-sizing: border-box;
  padding-left: 20px;
  margin: 0;
  text-align: left;
}

.ex__row2 {
  grid-row-start: 2;
  grid-row-end: 3;
  grid-column-start: 1;
  grid-column-end: 2;
}

.ex__img-grid {
  display: grid;
  grid-template-rows: 1fr;
  background: rgba(78,74,68,.1);
}

.ex__img-grid--2col {
  grid-template-columns: repeat(2, 1fr);
  grid-column-gap: 10vw;
  padding: 30px 10vw;
}
.ex__img-grid--3col {
  grid-template-columns: repeat(3, 1fr);
  grid-column-gap: 3vw;
  padding: 20px 4vw;
}

.ex__img {
  /*background: rgba(0,0,0,.5);*/
  text-align: center;
  box-shadow: 0 0 10px rgba(0,0,0,.5);
  background: #000;
  border-radius: 4px;
}
.ex__img:hover {
  -webkit-animation: anim_ex-img-shdw-hov;
          animation: anim_ex-img-shdw-hov;
  -webkit-animation-duration: .5s;
          animation-duration: .5s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
  -webkit-animation-timing-function: ease-out;
          animation-timing-function: ease-out;
}

.ex__img img {
/*  width: auto;
  height: auto;
  max-width: 100%;*/
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center top;
  max-height: 400px;
  opacity: .9;
  border-radius: 4px;
}
.ex__img img:hover {
  -webkit-animation: anim_ex-img-hov;
          animation: anim_ex-img-hov;
  -webkit-animation-duration: .2s;
          animation-duration: .2s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
  -webkit-animation-timing-function: ease-out;
          animation-timing-function: ease-out;
}

/*.ex__row3 {
  grid-row-start: 3;
  grid-row-end: 4;
  grid-column-start: 1;
  grid-column-end: 2;
}*/

.ex__hdr {
  font-family: 'Roboto Condensed';
  font-weight: 700;
  font-size: 1.2em;
  margin: auto;
  color: rgba(90,126,125,1);
}

.ex__hdr span {}

.ex__hdr-txt {}

.ex__title {
  margin: auto;
  text-align: center;
  color: rgba(78,74,68,0);
  display: block;
  font-size: .2em;
  padding: 10px;
  font-weight: 500;
}

.ex__link {
  font-size: 17px;
  font-family: 'Roboto Condensed';
  font-weight: 600;
  color: #598583;
  text-shadow: 0 0 10px rgba(0,0,0,.1);
  text-decoration: none;
  line-height: 2em;
}
.ex__link:hover {
  text-decoration: underline;
}

.ex__link-block {
  max-width: 300px;
  margin: auto;
  /*background: #e0e6e0;*/
  background: rgba(194,215,214,.8);
  border: 1px solid #D0F04F;
  border-radius: 8px;
  padding: 7px 10px;
  /*box-shadow: 0 0 8px rgba(0,0,0,.1);*/
}

.ex__link-notes {
  font-size: 14px;
  text-align: left;
}

.ex__link-u,
.ex__link-p {
  padding: 2px 5px;
  margin: 4px auto;
  display: block;
}

.ex__skills-ul {
  margin: 20px 0 0 15px;
  padding: 0;
}
.ex__skills-li {
  list-style-type: none;
  height: 28px;
  margin: 3px 0 0 0;
  padding: 0;
}
.ex__skills {
  color: rgba(100,151,149,0); 
  font-size: 14px;
  font-weight: 500;
  width: 0%;
  padding: 5px 10px;
  margin: 0;
  background: rgba(194,215,214,0);
  border-radius: 2px;
}

.ex__spacer {
  height: 220px;
  position: relative;
}
.ex__sep-bg {
  position: absolute;
  top: 0;
  left: 0;
  margin: 100px auto;
  height: 20px;
  width: 100%;
  background: #1B3336;
  box-shadow: 0 0 20px rgba(100,151,149,.5);
}
.ex__sep {
  position: absolute;
  top: 0;
  left: 0;
  height: 20px;
  width: 100%;
  background: -webkit-linear-gradient(45deg, rgba(204,0,0,0) 0%, rgba(204,0,0,0) 20%, rgba(204,0,0,1) 50%, rgba(204,0,0,0) 80%, rgba(204,0,0,0) 100%);
  background: linear-gradient(45deg, rgba(204,0,0,0) 0%, rgba(204,0,0,0) 20%, rgba(204,0,0,1) 50%, rgba(204,0,0,0) 80%, rgba(204,0,0,0) 100%);
  background-size: 200%;
  background-position: -100% top;
  box-shadow: 0 0 5px rgba(204,0,0,.4);
}

.ex__sep.show {
  background-position: -100% top;
  -webkit-animation: anim_ex-sep;
          animation: anim_ex-sep;
  -webkit-animation-duration: 2s;
          animation-duration: 2s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
  -webkit-animation-timing-function: ease-out;
          animation-timing-function: ease-out;
}


.active {
  -webkit-animation: anim_ex-bg-active;
          animation: anim_ex-bg-active;
  -webkit-animation-duration: .5s;
          animation-duration: .5s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
  -webkit-animation-timing-function: ease-out;
          animation-timing-function: ease-out;
}

.inactive {
  -webkit-animation: anim_ex-bg-inactive;
          animation: anim_ex-bg-inactive;
  -webkit-animation-duration: .5s;
          animation-duration: .5s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
  -webkit-animation-timing-function: ease-out;
          animation-timing-function: ease-out;
}

.ex__skills.show {
  border-left: 2px solid #D0F04F;
  -webkit-animation: anim_ex-skills-show;
          animation: anim_ex-skills-show;
  -webkit-animation-delay: .5s;
          animation-delay: .5s;
  -webkit-animation-duration: .2s;
          animation-duration: .2s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
  -webkit-animation-timing-function: ease-out;
          animation-timing-function: ease-out;
}

.active .ex__hdr {
  -webkit-animation: anim_ex-hdr-bg;
          animation: anim_ex-hdr-bg;
  -webkit-animation-duration: .3s;
          animation-duration: .3s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
  -webkit-animation-timing-function: ease-in;
          animation-timing-function: ease-in;
}



@-webkit-keyframes anim_ex-hdr-bg {
  0% { color: rgba(90,126,125,1); }
  100% { color: rgba(78,74,68,1); }
}


@keyframes anim_ex-hdr-bg {
  0% { color: rgba(90,126,125,1); }
  100% { color: rgba(78,74,68,1); }
}

@-webkit-keyframes anim_ex-sep {
  0% { background-position: -100% top; }
  100% { background-position: 100% top; }
}

@keyframes anim_ex-sep {
  0% { background-position: -100% top; }
  100% { background-position: 100% top; }
}

@-webkit-keyframes anim_ex-skills-show {
  0% { width: 0%; color: rgba(100,151,149,0); background: rgba(194,215,214,0); }
  100% { width: 100%;  color: rgba(100,151,149,1); background: rgba(194,215,214,1); }
}

@keyframes anim_ex-skills-show {
  0% { width: 0%; color: rgba(100,151,149,0); background: rgba(194,215,214,0); }
  100% { width: 100%;  color: rgba(100,151,149,1); background: rgba(194,215,214,1); }
}

@-webkit-keyframes anim_ex-img-hov {
  0% { opacity: .8; }
  100% { opacity: 1; }
}

@keyframes anim_ex-img-hov {
  0% { opacity: .8; }
  100% { opacity: 1; }
}

@-webkit-keyframes anim_ex-img-shdw-hov {
  0% { box-shadow: 0 0 10px rgba(0,0,0,.5); }
  100% { box-shadow: 0 0 20px rgba(0,0,0,.7); }
}

@keyframes anim_ex-img-shdw-hov {
  0% { box-shadow: 0 0 10px rgba(0,0,0,.5); }
  100% { box-shadow: 0 0 20px rgba(0,0,0,.7); }
}
  

@-webkit-keyframes anim_ex-left {
  0% { margin-left: -100px; opacity: 0; }
  90% { margin-left: 8px; opacity: 1; }
  100% { margin-left: 0; opacity: 1; }
}

@keyframes anim_ex-left {
  0% { margin-left: -100px; opacity: 0; }
  90% { margin-left: 8px; opacity: 1; }
  100% { margin-left: 0; opacity: 1; }
}

@-webkit-keyframes anim_ex-title {
  0% { color: rgba(78,74,68,0); font-size: .2em;}
  100% { color: rgba(78,74,68,.7); font-size: 1em; }
}

@keyframes anim_ex-title {
  0% { color: rgba(78,74,68,0); font-size: .2em;}
  100% { color: rgba(78,74,68,.7); font-size: 1em; }
}






@media (max-width: 767px) {

  .ex__wrap,
  .ex__row1,
  .ex__row1-left,
  .ex__row1-right,
  .ex__img {
    display: block;
  }

  .ex__skills,
  .ex__skills-ul {
    margin: auto;
    text-align: center;
  }
  .ex__skills-li {
    display: inline-block;
    margin: 5px;
  }

}
