.resume {
  position: relative;
  box-sizing: border-box;
  z-index: 0;
  background: #f5f5f5;
}

/***** PAGE HEADER ******/

.resume__hdr-wrap {
	margin: 0 auto;
	text-align: center;
	width: 100%;
	z-index: 1000 !important;
	box-sizing: border-box;
	font-size: calc(1em + 1vmin);
	background: 
		linear-gradient(45deg, rgba(19,19,19,.2) 0%, rgba(19,19,19,.1) 30%, rgba(19,19,19,.05) 100%),
		/*radial-gradient(rgba(208,240,79,.5) 0%, rgba(208,240,79,0) 100%),*/
		radial-gradient(rgba(255,255,255,.5) 0%, rgba(255,255,255,0) 100%),
		/*linear-gradient(to bottom, rgba(0,230,223,0) 0%, rgba(0,230,223,.1) 100%),*/
		linear-gradient(to bottom, rgba(208,240,79,0) 0%, rgba(208,240,79,.1) 100%),
		#f5f5f5;
		/*rgba(33,122,223,1);*/
	padding-top: 100px;
	padding-bottom: 100px;
	/*border-bottom: 3px solid #aecac9;*/
	border-bottom: 3px solid #AECAC9;
}
.resume__hdr {
  font-size: calc(1.2em + 1vmin);
  	z-index: 1000 !important;
}

.resume__hdr-hdr {
	font-family: 'Roboto Condensed';
	font-weight: 700;
	font-style: italic;
	color: #000000;
	background: 
		linear-gradient(to bottom, #cc0000 0%, #cc0000 30%, #000 100%);
	background:
		-webkit-linear-gradient(to bottom, #cc0000 0%, #3f3f53 100%);
	-webkit-background-clip: text;
	-webkit-text-fill-color: transparent;
	z-index: 1000 !important;
}

.resume__hdr-txt {
  padding: 0 calc(10 * 1.5vw);
	margin: auto;
	color: #628a88;
		z-index: 1000 !important;
}

/***** END PAGE HEADER ******/

.resume__body {
	width: 80%;
	margin: auto;
	padding-top: 20px;
	z-index: 1000 !important;
	/*background: #f3f3f3;*/
}

.resume__job {
	padding-bottom: 20px;
	margin-bottom: 50px;
	border-bottom: 1px solid rgba(0,0,0,.1);
}

.resume__job-title {
	font-weight: 600;
	text-transform: uppercase;
	font-size: calc(10px + 1vmin);
	font-style: italic;
}

.resume__job-loc-date {
	padding-bottom: 20px;
	display: block;
}

.resume__job-loc {
	float: left;
	color: #666;
	font-weight: 600;
	font-size: calc(7px + 1vmin);
}

.resume__job-date {
	float: right;
	color: #666;
	font-size: calc(7px + 1vmin);

}

.resume__job-desc {
	clear: both;
}

.resume__job-skills {
	font-size: .9em;
	color: #666;
	background: rgba(150,150,150,.1);
	margin: 20px auto;
}

.resume__job-skills li {
	display: inline-block;
	list-style-type: none;
	margin: 7px calc(2px + 1vmin);
}



