.photo-gallery__grid {
  width: 100%;
  max-width: 1200px;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: repeat(3, 1fr);
  grid-column-gap: 0px;
  grid-row-gap: 0px;
  padding: 0;
  margin: 0 auto;
}

.photo-gallery__grid-item {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  /*display: flex;*/
  /*flex-direction: column;*/
  /*border: 1px solid green;*/

}
.photo-gallery__grid-item img {
  width: auto;
  height: auto;
  max-width: 100%;
  display: flex;
}

.photo-gallery__grid-item-1 { grid-area: 1 / 1 / 2 / 2; } /* mt ch  */
.photo-gallery__grid-item-2 { grid-area: 1 / 2 / 2 / 3; } /* jss  */
.photo-gallery__grid-item-3 { grid-area: 1 / 3 / 2 / 4; } /* gc self  */
.photo-gallery__grid-item-4 { grid-area: 1 / 4 / 3 / 5; } /* campfire  */
.photo-gallery__grid-item-5 { grid-area: 2 / 1 / 3 / 2; } /* ledge  */
.photo-gallery__grid-item-6 { grid-area: 2 / 2 / 3 / 3; } /* santa */
.photo-gallery__grid-item-7 { grid-area: 2 / 3 / 3 / 4; } /* grassy  */
.photo-gallery__grid-item-8 { grid-area: 3 / 1 / 4 / 2; } /* tri  */
.photo-gallery__grid-item-9 { grid-area: 3 / 2 / 4 / 3; } /* nrim  */
.photo-gallery__grid-item-10 { grid-area: 3 / 3 / 4 / 5; } /* tm  */


@media (max-width: 800px) {

  .photo-gallery__grid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: repeat(4, 1fr);
  }

  .photo-gallery__grid-item-1 { grid-area: 1 / 1 / 2 / 2; } /* mt ch  */
  .photo-gallery__grid-item-2 { grid-area: 1 / 2 / 2 / 3; } /* jss  */
  .photo-gallery__grid-item-3 { grid-area: 2 / 1 / 3 / 2; } /* gc self  */
  .photo-gallery__grid-item-4 { grid-area: 1 / 3 / 3 / 4; } /* campfire  */
  .photo-gallery__grid-item-5 { grid-area: 3 / 1 / 4 / 2; } /* ledge  */
  .photo-gallery__grid-item-6 { grid-area: 2 / 2 / 3 / 3; } /* santa  */
  .photo-gallery__grid-item-7 { grid-area: 3 / 2 / 4 / 3; } /* grassy  */
  .photo-gallery__grid-item-8 { grid-area: 4 / 1 / 5 / 2; } /* tri  */
  .photo-gallery__grid-item-9 { grid-area: 3 / 3 / 4 / 4; } /* nrim  */
  .photo-gallery__grid-item-10 { grid-area: 4 / 2 / 5 / 4;} /* tm  */

}

@media (max-width: 600px) {

  .photo-gallery__grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(6, 1fr);
  }

  .photo-gallery__grid-item-1 { grid-area: 1 / 1 / 2 / 2; } /*  mt ch  */
  .photo-gallery__grid-item-2 { grid-area: 1 / 2 / 2 / 3; } /*  jss  */
  .photo-gallery__grid-item-3 { grid-area: 6 / 1 / 7 / 2; } /* gc self  */
  .photo-gallery__grid-item-4 { grid-area: 2 / 2 / 4 / 3; } /* campfire  */
  .photo-gallery__grid-item-5 { grid-area: 6 / 2 / 7 / 3; } /* ledge  */
  .photo-gallery__grid-item-6 { grid-area: 2 / 1 / 3 / 2; } /* santa  */
  .photo-gallery__grid-item-7 { grid-area: 3 / 1 / 4 / 2; } /* grassy  */
  .photo-gallery__grid-item-8 { grid-area: 4 / 1 / 5 / 2; } /* tri  */
  .photo-gallery__grid-item-9 { grid-area: 4 / 2 / 5 / 3; } /* nrim  */
  .photo-gallery__grid-item-10 { grid-area: 5 / 1 / 6 / 3; } /* tm  */

}